
import router from "@/router";

export default {
  name: "IPPopup",
  props: ['place'],

  mounted() {
    window.addEventListener("keydown", this.escKeyListener);
  },

  unmounted() {
    window.removeEventListener("keydown", this.escKeyListener);
  },

  methods: {
    closeDetail() {
      router.push({name: 'Home'});
    },

    escKeyListener(event) {
      if (event.key === "Escape") {
        this.closeDetail();
      }
    },
  }
}
